@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;0,600;0,700;1,400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Source Sans Pro", sans-serif;
}

#main {
	height: 100vh;
}

#main::after {
	content: "";
	background: url(/images/mobile-background.jpg);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: -999;
	opacity: 0.5;
}

/* media queries */
@media screen and (min-width: 750px) {
	#main::after {
		background: url(/images/background-img.jpg);
		background-size: cover;
		opacity: 0.9;
		-webkit-filter: brightness(60%);
		        filter: brightness(60%);
	}
}

#about {
	background-color: rgba(0, 0, 0, 0.85);
	padding: 70px 0;
	color: rgb(255, 255, 255);
}

.about-wrapper {
	display: flex;
	flex-direction: column;
}

.about-profile {
	display: none;
}

.content {
	width: 85%;
	margin: 0 auto;
}

.title {
	text-align: center;
	letter-spacing: 4px;
	margin-bottom: 30px;
}

.text {
	text-align: center;
	line-height: 2em;
}

.contact {
	margin-top: 70px;
}

/* media queries */
@media screen and (min-width: 750px) {
	#about {
		padding: 100px 0;
	}

	.about-wrapper {
		flex-direction: row;
		width: 80%;
		margin: 0 auto;
	}

	.content {
		width: 60%;
		margin: 0 auto;
	}

	.title {
		margin-bottom: 45px;
		text-align: left;
	}

	.text {
		line-height: 2.5em;
		text-align: left;
	}

	.contact {
		margin-top: 90px;
	}

	.about-profile {
		display: block;
		width: 15%;
		height: 15%;
		min-height: 100px;
		margin: 0 auto;
		border-radius: 50%;
	}
}

#contact {
	text-align: center;
}

.scroll-up-icon,
.contact-text {
	color: #fff;
}

.scroll-up {
	background-color: transparent;
	margin-bottom: -80px;
	margin-top: 20px;
}

.scroll-icon {
	background-color: rgba(0, 0, 0);
	width: 130px;
	height: 120px;
	margin: 0 auto;
	border-radius: 50%;
}

.scroll-up-icon {
	font-size: 2em;
	cursor: pointer;
	color: rgba(255, 255, 255, 0.6);
	margin-top: 11px;
}

.scroll-up-icon:hover {
	color: rgba(255, 255, 255);
}

.social-icons {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	margin: 25px auto;
	width: 70%;
}

.icon {
	font-size: 20px;
	color: rgba(255, 255, 255, 0.6);
}

.icon:hover {
	color: #fff;
}

.contact-info > h3 {
	color: #fff;
	letter-spacing: 2px;
	text-transform: uppercase;
}

.contact-info {
	padding: 30px 0;
	background-color: rgba(0, 0, 0);
}

.contact-text,
.contact-text .span {
	font-size: 15px;
	letter-spacing: 1px;
}

/* media queries */
@media screen and (min-width: 750px) {
	.contact-info > h3 {
		font-size: 20px;
		letter-spacing: 4px;
		padding: 10px 0;
	}

	.icon {
		font-size: 30px;
	}

	.social-icons {
		width: 40%;
	}

	.contact-text,
	.contact-text .span {
		font-size: 18px;
		letter-spacing: 2px;
	}
}

@media screen and (min-width: 1400px) {
	.social-icons {
		width: 25%;
	}
}

#home {
	display: flex;
	flex-direction: column;
}

.profile {
	display: block;
	width: 30%;
	height: 30%;
	min-height: 100px;
	margin: 70px auto;
	border-radius: 50%;
}

.intro {
	text-align: center;
	width: 90%;
	margin: 30px auto;
}

.name {
	margin-bottom: 20px;
	font-size: 2.3em;
	letter-spacing: 2px;
}

.hashtags {
	margin-top: 10px;
	text-transform: uppercase;
}

.scroll-down {
	text-align: center;
	margin-top: 40px;
}

.scroll-down-icon {
	font-size: 2em;
	cursor: pointer;
	color: rgba(0, 0, 0, 0.6);
}

.scroll-down-icon:hover {
	color: rgba(166, 43, 43);
}

/* Media Queries */
@media screen and (max-width: 370px) {
	.profile {
		margin: 35px auto;
		min-height: 100px;
	}
}

@media screen and (min-width: 750px) {
	.intro {
		width: 100%;
	}

	.scroll-down-icon {
		font-size: 2.5em;
	}

	#home {
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		height: 100vh;
	}

	.profile {
		display: none;
	}

	.name {
		margin-bottom: 65px;
		font-size: 5em;
	}

	.intro {
		margin-top: 100px;
		color: #fff;
	}

	.intro-text {
		line-height: 30px;
		letter-spacing: 1px;
		text-shadow: 2px 2px #000;
	}

	.hashtags {
		margin-top: 30px;
		text-shadow: 2px 2px #000;
	}

	.scroll-down {
		margin-top: -50px;
	}

	.scroll-down-icon {
		color: #fff;
	}

	.scroll-down-icon:hover {
		color: rgb(235, 102, 14);
	}
}

.nav-bar {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

.mobile-nav-icon {
	width: 100%;
	font-size: 2em;
	margin: 10px 0 0 20px;
	cursor: pointer;
}

.nav-list {
	list-style: none;
	text-align: center;
	top: 65px;
	left: -100%;
	position: absolute;
	transition: all 0.5s;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	height: auto;
	min-height: 540px;
	opacity: 0;
	text-transform: uppercase;
}

.nav-list-active {
	top: 65px;
	left: 0;
	position: absolute;
	transition: all 0.5s ease;
	list-style: none;
	text-align: center;
	width: 100%;
	height: auto;
	min-height: 540px;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	opacity: 1;
	z-index: 1;
	text-transform: uppercase;
	background-color: rgb(0, 0, 0);
}

.scrolled {
	display: none;
}

.nav-container {
	width: 100%;
	padding: 50px 0;
}

.nav-container:hover {
	background-color: #fff;
}

.nav-container:hover .nav-link {
	color: #000;
}

.nav-link {
	text-decoration: none;
	color: rgb(255, 255, 255);
	letter-spacing: 2px;
	font-weight: 500;
}

/* Media Queries */
@media screen and (min-width: 750px) {
	.nav-list {
		min-height: 1280px;
		top: 85px;
		position: -webkit-sticky;
		position: sticky;
	}

	.nav-list-active {
		min-height: 1280px;
		top: 85px;
	}

	.nav-bar {
		flex-wrap: nowrap;
	}

	.mobile-nav-icon {
		display: none;
	}

	.scrolled {
		display: flex;
		position: fixed;
		background-color: #000;
		z-index: 999;
	}

	.nav-list {
		width: 80%;
		top: 0;
		left: auto;
		margin: 0 auto;
		flex-direction: row;
		opacity: 1;
		height: 60px;
		min-height: -webkit-fit-content;
		min-height: -moz-fit-content;
		min-height: fit-content;
		justify-content: space-evenly;
	}

	.nav-container {
		width: -webkit-fit-content;
		width: -moz-fit-content;
		width: fit-content;
		padding: 0;
	}

	.nav-container:hover {
		background-color: transparent;
	}

	.nav-container:hover .nav-link {
		color: rgb(235, 102, 14);
	}

	.nav-link {
		color: rgb(255, 255, 255);
		position: relative;
	}

	.nav-link::before {
		content: " ";
		position: absolute;
		width: 100%;
		height: 2px;
		background-color: rgb(235, 102, 14);
		left: 0;
		transform: scaleX(0);
		transition: all 0.5s ease;
	}

	.nav-link::before {
		bottom: 0;
		transform-origin: center;
	}

	.nav-link:hover::before {
		transform: scaleX(1);
	}
}

.work-wrap {
	display: flex;
	flex-direction: column;
	text-align: center;
}

.education,
.work,
.skill {
	display: flex;
	flex-direction: column;
	margin: 40px auto 20px auto;
	width: 100%;
}

.education .work-title,
.work .work-title,
.skill .work-title {
	text-transform: uppercase;
}

.education .description > h4,
.description > p {
	width: 80%;
	margin: 0 auto;
}

/* education */
.education .description > h4 {
	font-size: 25px;
}

.major {
	font-weight: 600;
	font-style: italic;
}

/* works */
.work-title > h1 {
	position: relative;
	letter-spacing: 3px;
	font-size: 20px;
	width: 80%;
	margin: 0 auto 10px auto;
}

.work-title > h1::after {
	content: " ";
	position: absolute;
	width: 100%;
	height: 2px;
	background-color: #0881f1;
	left: 0;
	bottom: 0;
}

.project-wrapper {
	display: flex;
}

.work1-margin > a > img {
	margin-top: 10px;
}

.work .description {
	display: flex;
	flex-direction: column;
}

.comment {
	margin-bottom: 30px;
	opacity: 0.3;
	font-style: italic;
}

.work-info {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 0 auto;
	margin-bottom: 40px;
}

.project-image {
	position: relative;
	width: 100%;
	margin: 0 auto;
}

/* image with no link */
.project-image > img {
	width: 200px;
	height: 150px;
}

.project-image > img:hover {
	-webkit-filter: brightness(40%);
	        filter: brightness(40%);
	transition: all 0.5s ease;
}

/* image with link */
.project-image > a > img {
	width: 200px;
	height: 150px;
}

.project-image > a > img:hover {
	-webkit-filter: brightness(40%);
	        filter: brightness(40%);
	transition: all 0.5s ease;
}

.link-icon {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	opacity: 0;
	color: #fff;
	font-size: 20px;
}

.project-image:hover .link-icon {
	opacity: 1;
	transition: all 0.5s ease;
}

.project-title {
	width: 200px;
}

.project-description {
	display: none;
}

/* skills section */
.skill .description {
	display: flex;
	flex-direction: column;
}

.skill-name {
	font-size: 27px;
}

.skill-name > p {
	margin-bottom: 15px;
}

/* icon colors */
.html {
	color: #ff4500;
}

.css {
	color: #0881f1;
}

.js {
	color: #c7bd32;
}

.react {
	color: #0454a0;
}

.python {
	color: #0f58f7;
}

.sql {
	color: #6b6a6a;
}

.skill-name > p {
	font-size: 15px;
}

/* skill bars */
.skill-bars {
	display: none;
}

/* media queries */
@media screen and (min-width: 750px) {
	.work-wrap {
		text-align: left;
		width: 60%;
		margin: 0 auto;
		padding: 60px 0;
	}

	.description {
		width: 60%;
	}

	.education,
	.work,
	.skill {
		flex-direction: row;
		width: 100%;
		justify-content: flex-start;
	}

	.education .description > h4,
	.description > p {
		width: 100%;
	}

	.education .description > h4 {
		margin-bottom: 15px;
	}

	.work-title > h1 {
		width: 100%;
		margin: 0;
	}

	.work-title {
		margin-bottom: 15px;
	}

	.work-info {
		margin: 0 0 20px 0;
	}

	.project-image {
		width: auto;
		max-width: 200px;
		margin: 0;
	}

	.project-title {
		text-align: center;
	}

	.skill .description {
		flex-direction: row;
	}

	.education-wrapper,
	.work-wrapper,
	.skill-wrapper {
		display: flex;
		justify-content: space-between;
		width: 100%;
	}

	.skill-bars {
		display: block;
		background-color: #c5c2c2;
		height: 15px;
		width: 100%;
		margin-bottom: 10px;
		margin-top: -5px;
		border-radius: 5px;
	}

	/* skill process bars */
	.skill-bar-html,
	.skill-bar-css,
	.skill-bar-js,
	.skill-bar-react,
	.skill-bar-python,
	.skill-bar-sql {
		background-color: #000;
		height: 15px;
	}

	.skill-bar-html {
		width: 80%;
	}

	.skill-bar-css {
		width: 80%;
	}

	.skill-bar-js {
		width: 75%;
	}

	.skill-bar-react {
		width: 50%;
	}

	.skill-bar-python {
		width: 45%;
	}

	.skill-bar-sql {
		width: 60%;
	}

	.skill-name {
		width: 100%;
	}
}

@media screen and (min-width: 1020px) {
	.project-description {
		display: block;
		margin-left: 40px;
	}
}

